.btn-register{
    background-color: white ; /* Green */
    border: none;
    color: #00C1D4;
    width: 160px;
    height: 40px;
    border-radius: 15px;
    padding: 6px 30px;
    text-align: center;
    border:  2px solid #00C1D4;
    transition: 0.5s;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}
.btn-register:hover{
    background-color: #001E6C ;
    color: white;
    border:  2px solid #001E6C;
    transition: all 0.5s;
}